export default {
  install(Vue) {
    
    /**Notificaciones */
    const notification = {
      success(texto) {
        Vue.prototype.$notify({
          group: 'notification',
          title: 'xito',
          text: texto,
          type: 'success'
        });
      },
      error(texto) {
        Vue.prototype.$notify({
          group: "notification",
          title: 'Error',
          text: texto,
          type: "error"
        });
      },
      warning(texto) {
        Vue.prototype.$notify({
          group: "notification",
          title: "Advertencia",
          text: texto,
          type: "warning"
        });
      },
      info(texto) {
        Vue.prototype.$notify({
          group: "notification",
          title: "Informacin",
          text: texto,
          type: "info"
        });
      },
    };
    Vue.prototype.$notification = notification;

  }
}