<template>
  <div id="app">
    <notifications
      group="notification"
      position="bottom left"
      :duration="6000"
      :max="2"
      width="100%"
    >
      <template slot="body" slot-scope="props">
        <a-alert
          :message="props.item.title"
          :description="props.item.text"
          :type="props.item.type"
          show-icon
        />
      </template>
    </notifications>
    <div v-if="showList">
      <a-textarea 
        v-model="options" 
        placeholder="Ingrese opciones" 
        :rows="10" 
        @change="this.splitOptions"
        
      />
      <a-button type="primary"
        @click="this.loadOptions"
      >
      Cargar
    </a-button>
  </div>
  <diV v-else>
    <Wheel :segments="formattedOoptions"/>
  </diV>

  
  </div>
</template>

<script>

import Wheel from './components/Wheel.vue'

export default {
  components: {
    Wheel
  },
  
  data() {
    return {
      formattedOoptions:[],
      options: '',
      lstOptions: [],
      colours: ['rgb(253,162,135)','rgb(235,109,121)','rgb(112,66,105','rgb(115,101,162)','rgb(96,81,102)','rgb(51,69,101)',
      'rgb(253,162,135)','rgb(235,109,121)','rgb(112,66,105','rgb(115,101,162)','rgb(96,81,102)','rgb(51,69,101)',
      'rgb(253,162,135)','rgb(235,109,121)','rgb(112,66,105','rgb(115,101,162)','rgb(96,81,102)','rgb(51,69,101)',
      'rgb(253,162,135)','rgb(235,109,121)','rgb(112,66,105','rgb(115,101,162)','rgb(96,81,102)','rgb(51,69,101)',
      'rgb(253,162,135)','rgb(235,109,121)','rgb(112,66,105','rgb(115,101,162)','rgb(96,81,102)','rgb(51,69,101)'],

      showList:true
    }
  },
  methods: {
    splitOptions() {
    this.lstOptions = this.options.split(/\r?\n/)
   },
   toConvert(lstOptions) {
    let option; 
    lstOptions.map((item,index) => {
      option = new Object();
      option.textFillStyle = 'white'
      option.fillStyle = this.colours[index]
      option.text = item

      this.formattedOoptions.push(option)
      console.log(this.formattedOoptions)
    })
  
   
   },
   loadOptions() {
    this.toConvert(this.lstOptions)
    this.showList = false
   }
  }

};
</script>


